import React, {useState} from 'react';

const shuffle = array => array.sort(() => Math.random() - 0.5);
const d = shuffle([
  '1jfej9oSnoKhSNpsKL1nkWckZRxGulJYA',
  '1C4qweD6S4I9JaoVw9hZo2j7PoUl-Ya5m',
  '1bS83RPFULEjS73TixOScbFF5bW-aIdv-',
  '1hpiXKoyG8aTxKY_Swz10VhEjMC2xR1WA',
  '19R0l7jeT3VcGYXH5VCbSyOVsAycV1oPk',
  '1q9pmpnLjWZAolbV-DqQ-clSDRp9FmbjX',
  '1xSQbdZRbUJKv4eMUV9Qm1BC52WC0jmf-',
]);

const Fotox = () => {
  const [cur, setCur] = useState(0);
  
  return (
    <div style={{height: '100vh', width: '100vw'}} onClick={() => setCur(cur === d.length - 1 ? 0 : cur + 1)}>
      <img src={`https://drive.google.com/uc?export=view&id=${d[cur]}`} alt=""/>
    </div>
  );
};

export default Fotox;
